cc.Class({
    extends: cc.Component,

    properties: {
        starCountLabel: {
            default: null,
            type: cc.Label,
            tooltip: '显示星星数量的Label组件'
        }
    },

    onLoad() {
        this.starCount = 0;
        this.updateStarDisplay();
    },

    addStar() {
        this.starCount++;
        this.updateStarDisplay();
    },

    updateStarDisplay() {
        if (this.starCountLabel) {
            this.starCountLabel.string = `Stars: ${this.starCount}`;
        }
    }
});
